        ["Total",""]
      ],
      section5:[
        ["Numerador",""],
        ["Denominador",""]
      ]
    }
  },
  methods: {
    check_one(i){
      this.additional[i] = [];
    },
    check_two(i){
      this.additional2[i] = [];
    },
    setOptions (e,i,x) {
      console.log(i);
      const value = e.target.value;
      const rpta = this.section3[x].map((k)=>{
        if (k[0] == i+1){
          if (k[1] == 'NO'){
            k[1] = value;
          }else{
            k[1] = 'NO'
          }
        }
        return k;
      })
      this.section3[x] = rpta;
      console.log(this.section3[x])
    }
  }
}
